import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import Home from './Home';
import Week01 from './weeks/week01/Week01';
function App() {
  return (
    <Router>
      <div className='App'>
        <Switch>
          <Route path='/week01'>
            <Week01 />
          </Route>
          <Route path='/'>
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
