import React from 'react';
import { Link } from 'react-router-dom';

function Home() {
  const links = [
    {
      label: 'Week 01',
      url: '/week01',
    },
    {
      label: 'Week 02',
      url: '/week02',
    },
  ];
  return (
    <div>
      <h1>Web Development II Frontend</h1>

      <ul>
        {links.map((link) => {
          return (
            <li>
              <Link to={link.url}>{link.label}</Link>
            </li>
          );
        })}
      </ul>
    </div>
  );
}

export default Home;
